header {
    margin-top: 6rem;
    height: calc(100vh - 5rem);
    display: grid;
    place-items: center;
    background: var(--color-white);
    transition: var(--transition);
}


.title_color {

    color: var(--color-primary);
}

.type_class {
    font-size: 1.5em;
    color: var(--color-primary);
    text-align: center;

}


.header__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.header__container p {
    width: 35rem;
    text-align: center;
    margin: 0.6rem 0 2rem;
}

.header__cta {
    display: flex;
    align-items: center;
    gap: 1.2rem;
}

.header__socials {
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    font-size: 30px;
    /* Adjust the font size to your desired value */
}

.header__socials a {
    width: 1.8rem;
    aspect-ratio: 1/1;
    background: var(--color-light);
    color: var(--color-black);
    display: grid;
    place-items: center;
    border-radius: var(--border-radius-3);
}

.header__socials a:hover {
    background: var(--color-primary);
    color: var(--color-white);
    transform: translateX(-0.7rem);
}



/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 1024px) {
    header {
        /* margin-top: 0; */
        height: 100vh;
    }
}

.header__profile {
    width: 18rem;
    height: 18rem;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 1.5rem;
    background-color: transparent;
}

@media screen and (max-width: 800px) {
    .header__socials {
        display: none;
    }
}

/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 600px) {
    .header__profile {
        width: 14rem;
        height: 14rem;
        border-radius: 50%;
        /* max-width: 60%;
        max-height: 35vh; */
    }

    .type_class {
        font-size: 1.5rem !important;
    }

    .header__container p {
        width: 100%;
    }

    .header__cta {
        gap: 1rem;
    }


}