.skills__container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem 4rem;

}

.card.skills {
    display: flex;
    align-items: center;
    margin: 10px;
    gap: 1rem;
    align-content: center;
}

.logo_icon {
    font-size: 2.5rem;
    color: var(--color-black);
    border-radius: var(--border-radius-3);
    width: fit-content;
    margin-inline: auto;
    padding: 0.6rem;
    display: flex;
    align-items: center;

}

.icon_size {
    font-size: 3.5rem;
    color: var(--color-black);
    border-radius: var(--border-radius-3);
    width: fit-content;
    margin: 10px;
    margin-inline: auto;
    padding: 0.6rem;
    display: flex;
    align-items: center;
}


.skills h4 {
    margin-bottom: 0.7rem;
}

.skill__item ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.skills__icon {
    font-size: 1.3rem;
    color: var(--color-black);
    border-radius: var(--border-radius-3);
    width: fit-content;
    margin-inline: auto;
    padding: 0.6rem;
    display: flex;
    align-items: center;
    transition: var(--transition);
}


.skills:hover .skills__icon {
    color: var(--color-primary);
}

.skill_trick {
    margin: 10px;
}

.skill_trick:hover .icon_size {
    color: var(--color-primary);
}


/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 1024px) {
    .skills__container {
        gap: 1.5rem;
    }
}


/* MEDIA QUERIES (phones) */
@media screen and (max-width: 600px) {
    .skills__container {
        grid-template-columns: 1fr;
        gap: 1.2rem;
    }

    .card.skills {
        gap: 1.2rem;
    }

    .icon_size {
        font-size: 2.5rem;
    }
}