.education__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem 4rem;

}

.card.educations {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
}

.edu_card_content {
  padding: 1.875rem 1.25rem 0.625rem;
}

.edu_card_content h3 {
  font-size: 24px;
  color: #313237;
  margin-bottom: 12px;
  font-weight: bold;
}

.edu_card_content p {
  color: #838c8d;
  font-size: 15px;
  margin: 0 0 20px 0;
}

.edu_card_content p:first-child {
  margin: 0 0 10px 0;
  color: #48959e;
}




/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 1024px) {
  .education__container {
    gap: 1.5rem;
  }
}


/* MEDIA QUERIES (phones) */
@media screen and (max-width: 600px) {
  .education__container {
    grid-template-columns: 1fr;
    gap: 1.2rem;
  }

  .card.educations {
    gap: 1.2rem;
  }
}