nav {
  width: 100vw;
  height: 5rem;
  display: grid;
  place-items: center;
  background: var(--color-primary);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  box-shadow: 0 1rem 1.5rem hsl(var(--primary-hue), 68%, 42%, 20%);
  transition: var(--transition);
}

.nav__container {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.navItem {
  color: white;
  font-size: 1.5rem;
  font-weight: 500;
  text-transform: capitalize;
  transition: var(--transition);
}

.nav__logo {
  width: 2.5rem;
  border-radius: 50%;
  overflow: hidden;
}

.nav__menu {
  display: flex;
  gap: 3.5rem;
}

.nav__container a{
  text-decoration: transparent;
  color: white;
}

.modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  /* Semi-transparent black background */
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background-color: #cc3e3e;
  padding: 20px;
  border-radius: 5px;
}

.close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
}




:local(#theme__icon) {
  background: transparent;
  color: white;
  font-size: 2rem;
  cursor: fixed;
}


/* HIDE NAV MENU ON TABLETS AND PHONES */
@media screen and (max-width: 1024px) {
  .nav__menu {
    display: none;
  }


}